<script>
  import mixins from '../../../assets/js/mixins'
  export default {
    mixins: [mixins.$exportDetail],
    data () {
      let vm = this
      return {
        nav: [{name: '教师招聘', path: 'go(-1)'}, {name: '应聘详情', path: ''}],
        id: vm.$route.query.id,
        teacherDetailList: [],
        resumesLength: null,
        awardsLength: null,
        dialogVisible: false,
        status: null,
        // 设置应聘结果
        Result: this.$enums.TeacherApplyStatus.list,
        ResultStatus: false,
        query:{
          id: vm.$route.query.id
        }
      }
    },
    async created () {
      await this.teacherDetail()
    },
    watch:{
      "dialogVisible": function () {
        if (!this.dialogVisible){
          this.status=null
        }
        if (this.dialogVisible){
          if (this.ResultStatus) return
          this.ResultStatus=true
          this.Result.shift()
          this.Result.pop()
        }
      }
    },
    methods: {
      //设置应聘结果
      onClickResult(){
        this.dialogVisible=true
      },
      async teacherDetail () {
        this.teacherDetailList = await this.$ajax({
          url: '/teacherApply/getTeacherApplyDetail.do',
          query: {
            id: this.id
          }
        })
        this.resumesLength = this.teacherDetailList.resumes.length + 4
        this.awardsLength = this.teacherDetailList.awards.length + 4
      },
      getName: function (val) {
        switch (val) {
          case 1:
            return '南校区'
          case 2:
            return '东校区'
          case 3:
            return '九华校区'
          default:
            return val
        }
      },
      async onSubmitStatus () {
        try {
          await this.$ajax({
            url: '/teacherApply/setTeacherApplyStatus.do',
            el: '.submit_loading',
            noText: true,
            query: {
              teacherApplyId: this.teacherDetailList.teacherApplyId,
              applyStatus: this.status
            }
          })
          await this.teacherDetail()
          this.$remind('设置成功')
          this.dialogVisible = false
        } catch (e) {
          this.dialogVisible = true
        }
      }
    }
  }
</script>
